<template>
  <div>
    <div class="search">
      <input type="text" class="search-input" placeholder="输入城市或拼音" v-model="keyword">
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
        <li v-for="item in  list" class="search-item border-bottom" :key="item.id"
        @click="handleCityClick(item.name)">{{item.name}}</li>
        <li class="search-item border-bottom" v-show="hasNoData">没有找到匹配数据</li>
      </ul>
    </div>
  </div>

</template>

<script>
  import BScroll from 'better-scroll'
  import {mapMutations } from 'vuex'
  export default {
    name: "CitySearch",
    props: {
      cities: Object
    },
    data() {
      return {
        keyword: '',
        list: [],
        timer: null
      }
    },
    computed: {
      hasNoData() {
        return !this.list.length
      }
    },
    watch: {
      keyword() {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        if (!this.keyword) {
          this.list = []
          return
        }
        this.timer = setTimeout(() => {
          const result = []
          for (let i in this.cities) {
            this.cities[i].forEach((value) => {
              if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
                result.push(value)
              }
            })
          }
          this.list = result
        }, 100)
      }
    },
    mounted() {
      this.scroll = new BScroll(this.$refs.search)
    },
    methods :{
      handleCityClick(city){
        this.$store.commit('changeCity',city)
        this.$router.push('/')  //改变城市后直接跳转到首页
      },
      ...mapMutations(['changeCity'])
    }
  }
</script>

<style lang="stylus" scoped>

  @import '../../../assets/styles/varibles.sty'
  .search
    height: .7rem
    background: $bgColor
    padding 0 .1rem
    .search-input
      box-sizing border-box
      height: .62rem
      line-height .62rem
      width: 100%
      height: .62rem
      text-align center
      border-radius: .06rem
      color #666

  .search-content
    z-index: 1
    overflow hidden
    position absolute
    top: 1.58rem
    left: 0
    right: 0
    bottom 0
    background #eee
    .search-item
      line-height: .62rem
      padding-left .2rem
      color: #666
      background #fff
</style>



